import { Component, EventEmitter, Inject, OnInit, Output } from '@angular/core';
import { IBanco } from '../../models/banco';
import { BancosService } from '../../service/bancos.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { AppAlertService } from 'app/shared/service/app/alert.service';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { finalize, Subject, takeUntil, takeWhile, tap, timer } from 'rxjs';
import { IAlertConfig } from '@shared/models/alert';
@Component({
    selector: 'app-save-banco',
    templateUrl: './save.component.html',
    styleUrls: ['./save.component.scss']
})
export class SaveComponent implements OnInit {
    columns = ['nombre', 'activo', 'acciones'];
    form: FormGroup;
    banco: IBanco;
    showAlert = false;
    _alertConfig: IAlertConfig;
    private _unsubscribeAll: Subject<any> = new Subject<any>();
    @Output() public creadoExitosamente: EventEmitter<void> = new EventEmitter<void>();
    @Output() public editadoExitosamente: EventEmitter<void> = new EventEmitter<void>();

    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        private _bancosService: BancosService,
        public alert: AppAlertService,
        public dialogRef: MatDialogRef<SaveComponent>
    ) {
        this.banco = this.data ? this.data : null;
    }

    ngOnInit(): void {
        this.newForm();
        this.subscribeAlert();
    }

    // Alert
    subscribeAlert() {
        this.alert.emitConfig.subscribe({
          next: (config: IAlertConfig) => {
            this.showAlert = true;
            this._alertConfig = {
              ...config,
              appearance: 'border',
            };
            let time = 2;
            timer(100, 1500)
              .pipe(
                finalize(() => {
                  this.showAlert = false;
                  this._alertConfig = null;
                }),
                takeWhile(() => time > 0),
                takeUntil(this._unsubscribeAll),
                tap(() => (time -= 1))
              )
              .subscribe();
          },
        });
    }

    newForm() {
        this.form = new FormGroup({
            nombre: new FormControl((this.banco ? this.banco.descripcion : ''), Validators.required),
            activo: new FormControl((this.banco ? (this.banco.activo ? 1 : 0) : 1)), // Se hace esa condicional porque en el componente
                                                                                     // grilla dinamica convertimos los valores para poder 
                                                                                     // mostrarlos en la columna correspondiente
        });
    }

    onSubmit() {
        if (!this.form.valid) {
            return this.alert.error('Revise Los Datos Ingresados');
        } else {
            this.banco = {
                ...this.banco,
                descripcion: this.form.get('nombre').value,
                activo: this.form.get('activo').value,
            }
            if (this.banco.idbanco) {
                this._bancosService.updateBanco(this.banco)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.editadoExitosamente.emit();
                                this.onCloseMenu();
                            }
                        }
                    });
            } else {
                this._bancosService.saveBanco(this.banco)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.creadoExitosamente.emit();
                                this.onCloseMenu();
                            }
                        }
                    });
            }
        }
    }

    onCloseMenu() {
        this.dialogRef.close(false)
    }
}